import React, { useState, useEffect } from 'react';
import axios from 'axios';
import loading_gif from "../assets/spinner.gif";
import user_img from "../uploads/faceimage.jpg";

const localhost = "http://localhost:4000/";

function FSResult() {
  const [loading, setLoading] = useState(true);
  const [shape, setShape] = useState(null);
  const [confidence, setConfidence] = useState(null);

  useEffect(() => {
    const getResult = async () => {
      try {
        const response = await axios.get(localhost + "api/python_process");
        const result = response.data;
        setShape(result[0]);
        setConfidence(parseInt(result[1]));
      } catch (error) {
        console.log("실패", error);
      } finally {
        setLoading(false);
      }
    };

    getResult();
  }, []);

  return (
    <div>
      <p id='title'>{loading ? '얼굴 분석중...' : '분석 결과'}</p>
      <img id="result" src={loading ? loading_gif : user_img} alt="result" style={{ maxWidth: '250px' }} />
      <p id="paragraph">{!loading && `${shape}상에 ${confidence}% 일치!`}</p>
    </div>
  );
}

export default FSResult;
